import CollectionPlayer from "./CollectionPlayer";
import globalState from "@/store/globalState";
import util from "@/util";
import Employee from "@/Employee";
import ValueRange from "@/ValueRange";
import {identifier, list, object, primitive, raw, serializable} from "serializr";

export default class Research {

  @serializable
  private thumb: string;
  @serializable(identifier())
  public readonly name: string
  @serializable
  public readonly cost: number
  @serializable
  public readonly researchTime: number
  @serializable
  public readonly description: string;
  @serializable(list(primitive()))
  public readonly requires: string[] = []
  @serializable
  public readonly type: string = "" // farm, employee
  @serializable(raw())
  public readonly effects: any = {}

  @serializable(object(ValueRange))
  public completion: ValueRange
  @serializable
  public isBeingResearched: boolean = false

  constructor(info: any = {}) {
    this.name = info.title
    this.thumb = info.thumb
    this.description = info.description
    this.requires = info.requires
    this.cost = info.cost
    this.researchTime = info.researchTime
    this.type = (info.type) ? info.type : "employee"
    this.effects = (info.effects) ? info.effects : {}
    this.completion = new ValueRange(0, 0, 1 /*this.researchTime*/)
  }

  research(dt: number) {
    this.completion.value += dt
  }

  get isCompleted(): boolean {
    return this.completion.fraction >= 1
  }

  get isStarted(): boolean {
    return this.completion.fraction > 0
  }

  get image(): string {
    return util.baseUrl() + '/' + this.thumb
  }
}
