import CollectionPlayer from "@/CollectionPlayer";
import util from "@/util";
import ValueRange from "@/ValueRange";
import { reactive } from "vue";
import Research from "@/Research";
import {custom, identifier, list, object, primitive, raw, reference, serializable} from "serializr";
import ValueMod from "@/ValueMod";

export default class Employee {
  @serializable(custom(v => {}, (json, context) => new CollectionPlayer("liliana", context.json.framesInfo)))
  private player: CollectionPlayer = new CollectionPlayer()
  @serializable(raw())
  private readonly framesInfo: any = {}

  @serializable(identifier(primitive))
  public readonly uuid: string = util.uuid()
  @serializable
  public name: string = ''

  // Stats
  @serializable(object(ValueRange))
  public iqStat: ValueRange = new ValueRange(90 + Math.random() * 40, 40, 180)
  @serializable(object(ValueRange))
  public productionStat: ValueRange = new ValueRange(Math.random() * 0, 0, 1)
  @serializable(object(ValueRange))
  public mood: ValueRange = new ValueRange(5, 0, 10, ["Depressed", "Sad", "Moody", "Content", "Happy", "Joyful", "Ecstatic"])
  @serializable(object(ValueRange))
  public moodDelta: ValueRange = new ValueRange(0, -1, 1)

  @serializable(object(ValueRange))
  public salary: ValueRange = new ValueRange(this.targetSalary, 0)
  @serializable
  private _money: number = 0
  @serializable
  public assignment: string = 'farm'

  @serializable(list(object(Research)))
  private equipment: Research[] = []

  constructor(name: string, framesInfo: any) {
    this.name = name

    if (framesInfo != undefined) {
      this.framesInfo = framesInfo
      this.player = new CollectionPlayer("liliana", this.framesInfo)
    }
  }

  update(dt: number) {
    if (this.assignment === 'farm') {
      this.productionStat.valueMods.assignmentAdd = new ValueMod('add', 0)
      this.productionStat.valueMods.assignmentMul = new ValueMod('mul', 1)
    } else {
      this.productionStat.valueMods.assignmentAdd = new ValueMod('add', 0)
      this.productionStat.valueMods.assignmentMul = new ValueMod('mul', 0.5)
    }

    this.mood.value += this.moodDelta.value * dt
  }

  equip(research: Research) {
    if (!this.canEquip(research)) return

    this.equipment.push(research)

    let effects = research.effects
    if (effects.iq) this.iq += effects.iq
    if (effects.production) this.productionStat.valueMods[research.name] = new ValueMod('add', effects.production)
  }

  unequip(research: Research){
    if (this.isEquipped(research)) {
      this.equipment.splice(this.equipment.findIndex(item => item.name === research.name), 1)

      let effects = research.effects
      if (effects.iq) this.iq -= effects.iq
      if (effects.production) this.productionStat.valueMods[research.name] = new ValueMod('add', 0)
    }
  }

  toggleEquip(research: Research) {
    if (this.isEquipped(research)) {
      this.unequip(research)
    } else {
      this.equip(research)
    }
  }

  canEquip(research: Research): boolean {
    return research.type == "employee" && !this.isEquipped(research);
  }

  isEquipped(research: Research): boolean {
    return this.equipment.find(e => e.name === research.name) != undefined
  }

  get targetSalary(): number {
    return (1 - this.bimbofactor) * 1 + this.productionStat.baseValue * 1
  }

  get bimbofactor(): number {
    return 1 - this.iqStat.fraction
  }

  get production(): number {
    let sum = this.productionStat.value
    return sum
  }

  get image(): string {
    let index = Math.floor(this.bimbofactor * this.player.activeSequence().getLastIndex())
    this.player.activeSequence().goto(index)
    return this.player.activeSequence().get()
  }

  set iq(value) {
    this.iqStat.value = value
  }

  get iq(): number {
    return this.iqStat.value
  }

  pay(amount: number) {
    this._money += amount
  }

  get money(): number {
    return this._money;
  }
}
