    <div class="status-panel">
      <TextButton @click="farm.research(farm.state.lab.researchByName(research.name))" :class="{'disabled-button': research.isCompleted || research.isBeingResearched}">
        <p v-if="!research.isStarted">Start Research ${{ research.cost.toFixed(0) }}</p>
        <p v-else-if="!research.isBeingResearched">Continue Research</p>
        <p v-else-if="research.isCompleted">Completed</p>
        <p v-else>Researching...</p>
      </TextButton>
      <ValueMeter :container="research.completion">
        <p class="meter-label">{{ research.completion.value.toFixed(0) }} / {{ research.completion.max.toFixed(0) }}</p>
