  .value-meter {
    height: 50px;
    background-color: #222;

    .milk-label {
      margin: 0;
      mix-blend-mode: exclusion;
      color: #fff;
      font-weight: bold;
    }
  }

  .milkfarm-navbar {
    display: grid;
    grid-template-columns: 200px [title] 1fr;

    @media (max-width: $mobile-width) {
      grid-template-columns: 1fr;
    }
  }

  .menu {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(auto-fit, 1fr);
    grid-template-rows: 100%;

    p {
      font-size: 1.5em;
      font-weight: bold;
    }
  }

  .game-title {
    background-color: #541A32;

    @media (max-width: $mobile-width) {
      display: none;
    }
  }

  .selected-tab {
    background-color: rgba(0, 0, 0, 0);
    cursor: default;

    &:hover {
      background-color: rgba(0, 0, 0, 0);
    }

    &:active {
      background-color: rgba(0, 0, 0, 0);
    }
  }
</style>
