import CollectionPlayer from "@/CollectionPlayer";
import util from "@/util";
import {list, map, primitive, serializable} from "serializr";

export default class ValueMod {
  @serializable
  private readonly type: string = 'add' // add, mul
  @serializable
  private readonly value: number = 0

  constructor(type: string, value: number) {
    this.type = type
    this.value = value
  }

  static activate(value: number, mod: ValueMod): number {
    if (mod.type === 'add') return value + mod.value
    else if (mod.type === 'mul') return value * mod.value

    return value
  }

  /**
   * Activate all mods on a value. This uses a predefined order of activation.
   * @param value the value to modify
   * @param mods
   */
  static activateAll(value: number, mods: ValueMod[]): number {
    for (let mod of mods) {
      if (mod.type === 'add') value += mod.value
    }

    for (let mod of mods) {
      if (mod.type === 'mul') value *= mod.value
    }

    return value
  }
}
